<h2 id="企业管理的目标到底是什么" tabindex="-1"><a class="header-anchor" href="#企业管理的目标到底是什么" aria-hidden="true">#</a> 企业管理的目标到底是什么？</h2>
<p>华为的任总说，企业管理目标就是建立流程型组织。
这样我们看到了，我们要去解决企业管理的核心问题，就是用正确的输入、正确的过程，产生正确的结果，带来客户的价值，实现组织的持续成功。
我们要通过流程型组织，来不断地打造一个组织具有生命力的系统，它是超越领导者个人生命的，它就跟江河湖海、日升日落一样，不断地循环，来实现这个组织滚滚向前、奔涌向前。
我们要超越任何个体的生命，去建立组织的生命。
我们要在流程型组织中不断地去循环，正确输入过程和产生正确的结果，带来客户价值。
这时候这个组织就是个永动机，就是个印钞机，它用正确的逻辑诞生正确的结果，那一定能够赚到很多钱。
所以企业到底要干什么，企业不是去设计所谓的产品和服务，这些都是表面载体。
企业的根本就是不断地打造流程型组织，去迎合客户，不断地驱动以奋斗者为本的变革，去改造组织里的每一个人，去面对客户、面对流程。
所以这就形成了我们整个系统的变革逻辑，我们一个组织就是要成为一家笨公司，不断地以流程型组织来驱动组织的生命，永远面向客户，永远面向胜利。</p>
